import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import 'rxjs/Rx';
import { Subject } from 'rxjs/Subject';
import { LoggerService } from '../logger.service';
import { Observable } from 'rxjs/Observable';
import { ResultParser } from '../../results/result-parser';

@Injectable()
export class ApiService {
  private FlightResultUrl: string = '/assets/flight-result.json';
  public onGetFlightResultsJson = new Subject<any[]>();
  private requestOptions = new RequestOptions();

  constructor(private _http: Http, private _logger: LoggerService) {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json; charset=UTF-8');
    headers.append('Accept', 'application/json, text/javascript, */*; q=0.01');
    this.requestOptions.headers = headers;
  }

  getRequestId() {
    const url = 'TasAngular.aspx/GetRequestId';

    return this._http.post(url, {} , this.requestOptions)
      .map(
      (response: Response) => {
        try {
          const responseRequestIdJson = response.json();
          const requestId = JSON.parse(responseRequestIdJson.d);
          this._logger.logInfo(requestId);

          // Success
          return requestId;
        } catch (error) {
          // Error while parsing.
          this._logger.onException((<Error>error));
          throw new Error('Error while parsing the Request id');
        }
      }
      ).catch(
      // Http Request error.
      (err: Response) => Observable.throw('Http Error while try to get Request Id'));
  }

  getFlightResults() {
    return this._http.post('TasAngular.aspx/GetFlightResults', {})
      .map((flightResponse: Response) => {
        try {
          const responseFlightResultsJson = flightResponse.json();
          const flightsReults = JSON.parse(responseFlightResultsJson.d);
          this._logger.logInfo(flightsReults);

          return flightsReults;
        } catch (error) {
          this._logger.onException((<Error>error));
          throw new Error('Error while parsing the flights results');
        }
      }).catch(
      // Http Request error.
      (err: Response) => Observable.throw(err));
  }

  getMockFlightResults() {
    return this._http.get(this.FlightResultUrl)
      .map((resposnse: Response) => {
        try {
          const flightResultsFullJson = resposnse.json();
          const resultParser = new ResultParser();
          const directionsArray = resultParser.getDirectionsArray(flightResultsFullJson);
          this.onGetFlightResultsJson.next(directionsArray);
        } catch (error) {
          this._logger.onException(error);
        }
      });
  }



}



