import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { ResultsListComponent } from './results/result-area/results-list/results-list.component';
import { ResultComponent } from './results/result-area/results-list/flight-result/flight-result.component';
import { ResultDetailComponent } from './results/result-area/results-list/flight-result/flight-result-leg/flight-result-leg.component';
import { WaitForResultsComponent } from './wait-for-results/wait-for-results.component';
import { ApiService } from './services/api/api.service';
import { HttpModule } from '@angular/http';
import { LoggerService } from './services/logger.service';
import { FiltersComponent } from './filters/filters.component';
import { FilterComponent } from './filters/filter/filter.component';
import { ResultAreaComponent } from './results/result-area/result-area.component';

@NgModule({
  declarations: [
    AppComponent,
    ResultsListComponent,
    ResultComponent,
    ResultDetailComponent,
    WaitForResultsComponent,
    FiltersComponent,
    FilterComponent,
    ResultAreaComponent
  ],
  imports: [
    BrowserModule,
    HttpModule
  ],
  providers: [ApiService, LoggerService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



