import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../services/api/api.service';

@Component({
  selector: 'app-result-area',
  templateUrl: './result-area.component.html',
  styleUrls: ['./result-area.component.css']
})
export class ResultAreaComponent implements OnInit {

  private _flightResultArray: any[];
  private _isFirstJson: boolean = true;

  constructor( private _apiService: ApiService ) { }

  ngOnInit() {
    this.subscribeToFlightResults();
  }

  subscribeToFlightResults() {
    this._apiService.onGetFlightResultsJson.subscribe(
      (flightResultArray: any[] ) => {
        if (this._isFirstJson) {
          this._flightResultArray = flightResultArray;

        }
        // else{

        // }
      }
    );
  }

}



