import { Component, OnInit, HostListener } from '@angular/core';

import { ResultsListComponent } from './results/result-area/results-list/results-list.component';
import { WaitForResultsComponent } from './wait-for-results/wait-for-results.component';
import { ApiService } from './services/api/api.service';
import { Response } from '@angular/Http';
import { LoggerService } from './services/logger.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  title = 'Tas Onlnine Booking';
  _isAngularStarted: boolean = false;
  requestId: string;

  constructor(private _apiService: ApiService , private _logger: LoggerService) {

  }

  ngOnInit(): void {

  }

  getRequestId(event) {
    this._apiService.getRequestId()
      .subscribe(
        ( requestId: any ) => {
          this.requestId = requestId;
          console.log(requestId);
        },
        error => {
         this._logger.onHttpError('getRequestId()' , error);
        }
      );
      event.preventDefault();

      return false;
  }

  getFlightResults() {
    try {
      this._apiService.getFlightResults()
      .subscribe(
        ( flightResults: any ) => {

        },
        error => {
        this._logger.onHttpError(' getFlightResults()', error);
        }
    );

    } catch (error) {
      this._logger.onError(error);
    }

  }

  getMockFlightResults() {
    try {
      this._apiService.getMockFlightResults()
      .subscribe(
        ( flightResults: any ) => {

        },
        error => {
        this._logger.onHttpError(' getFlightResults()', error);
        }
    );
    } catch (error) {
      this._logger.onError(error);
    }
  }


  //  Listen to window global property from outside angular.
  //  'isAngularStarted' - the property name, 'event.detail' - the new value.
  @HostListener('window:isAngularStarted', ['$event'])
  testListener(event) {
    this._isAngularStarted = event.detail;
    this._logger.logInfo('Ng started!');
  }

}



